'use client';

import React, { useState, useEffect, useRef, useCallback, useMemo } from 'react';
import { MdRefresh } from 'react-icons/md';
import VideoCard from './VideoCard';
import type { VideoItem } from '@/app/types/video';
import { THEME_COLOR } from '@/appConfig';
import DownloadAppModal from '../common/DownloadAppModal';

interface VideoListProps {
  videos: VideoItem[];
  loadMore: () => void;
  isLoading?: boolean;
  isLoadingMore?: boolean;
  isEmpty?: boolean;
  hasError?: boolean;
  hasMore?: boolean;
  tabType: 'movie' | 'tv' | 'anime' | 'short-drama';
  emptyMessage?: string;
  translations: {
    general_no_image?: string;
    general_loading?: string;
    general_net_fault?: string;
    general_retry?: string;
    general_watch_now?: string;
    general_hold?: string;
    [key: string]: string | undefined;
  };
  currentPage?: number;
  itemsPerPage?: number;
}

function VideoList({ 
  videos = [], 
  loadMore, 
  isLoading = false,
  isLoadingMore = false,
  isEmpty = false,
  hasError = false,
  hasMore = true,
  tabType,
  emptyMessage = 'æ²¡æœ‰æ‰¾åˆ°ç›¸å…³è§†é¢‘',
  translations,
  currentPage = 0,
  itemsPerPage = 24
}: VideoListProps) {
  const [showDownloadModal, setShowDownloadModal] = useState(false);
  const observerRef = useRef<IntersectionObserver | null>(null);
  const loadingRef = useRef<HTMLDivElement>(null);
  const prevVideosLengthRef = useRef(videos.length);
  const listRef = useRef<HTMLDivElement>(null);
  
  // ç¡®ä¿translationsåŒ…å«æ‰€æœ‰å¿…éœ€çš„ç¿»è¯‘é”®
  const enhancedTranslations = useMemo(() => ({
    ...translations,
    general_no_image: translations.general_no_image || 'æ— å›¾ç‰‡',
    general_loading: translations.general_loading || 'åŠ è½½ä¸­...',
    general_net_fault: translations.general_net_fault || 'ç½‘ç»œé”™è¯¯',
    general_retry: translations.general_retry || 'é‡è¯•'
  }), [translations]);
  
  // è®¾ç½®æ— é™æ»šåŠ¨
  useEffect(() => {
    if (!loadingRef.current) return;
    
    observerRef.current = new IntersectionObserver(
      (entries) => {
        if (entries[0].isIntersecting && !isLoading && !isLoadingMore && hasMore) {
          loadMore();
        }
      },
      { threshold: 0.1 }
    );
    
    observerRef.current.observe(loadingRef.current);
    
    return () => {
      if (observerRef.current) {
        observerRef.current.disconnect();
      }
    };
  }, [isLoading, isLoadingMore, hasMore, loadMore]);
  
  // å±•ç¤ºAppä¸‹è½½æç¤º
  const showDownloadTip = useCallback(() => {
    setShowDownloadModal(true);
  }, []);
  
  // å…³é—­ä¸‹è½½æç¤º
  const handleCloseDownloadModal = useCallback(() => {
    setShowDownloadModal(false);
  }, []);
  
  // é‡è¯•åŠ è½½
  const handleRetry = useCallback(() => {
    loadMore();
  }, [loadMore]);
  
  return (
    <div className="video-list p-4" ref={listRef}>
      {/* ç©ºçŠ¶æ€ */}
      {isEmpty && !isLoading && !hasError ? (
        <div className="empty-state flex flex-col items-center justify-center py-12">
          <p className="text-gray-500 dark:text-gray-400">{emptyMessage}</p>
        </div>
      ) : (
        <div className="grid grid-cols-3 gap-4">
          {videos.map((video, index) => (
            <VideoCard 
              key={`${video.id}-${index}`} 
              video={video} 
              tabType={tabType}
              showDownloadTip={showDownloadTip}
              translations={enhancedTranslations}
              isFirstPage={currentPage === 0 && index < itemsPerPage}
            />
          ))}
        </div>
      )}
      
      {/* åŠ è½½çŠ¶æ€ */}
      {(isLoading || isLoadingMore) && (
        <div className="loading flex justify-center items-center p-4">
          <div className={`animate-spin rounded-full h-6 w-6 border-b-2 border-[${THEME_COLOR}]`}></div>
          <span className="ml-2 text-gray-600 dark:text-gray-400">{enhancedTranslations.general_loading}</span>
        </div>
      )}
      
      {/* é”™è¯¯çŠ¶æ€ */}
      {hasError && (
        <div className="error-state flex flex-col items-center justify-center py-4">
          <p className="text-red-500 dark:text-red-400 mb-2">{enhancedTranslations.general_net_fault}</p>
          <button 
            className={`flex items-center px-4 py-2 bg-[${THEME_COLOR}] text-white rounded-full`}
            onClick={handleRetry}
          >
            <MdRefresh className="mr-1" />
            {enhancedTranslations.general_retry}
          </button>
        </div>
      )}
      
      {/* æ— é™æ»šåŠ¨è§¦å‘å™¨ */}
      {hasMore && !hasError && (
        <div ref={loadingRef} className="h-4 w-full"></div>
      )}
      
      {/* ä¸‹è½½APPå¼¹æ¡† */}
      <DownloadAppModal 
        isOpen={showDownloadModal}
        onClose={handleCloseDownloadModal}
        translations={translations}
        modalType={tabType === 'short-drama' ? 'short' : 'default'}
      />
    </div>
  );
}

export default React.memo(VideoList); 