import { useCallback, useState, useEffect, useRef, useMemo } from 'react';
import { CategoryType } from '../store/useAppStore';
import useSWRInfinite from 'swr/infinite';
import { VideoItem, VideoListResponse } from '../types/video';
import { getVideoList } from '../data/videos.js';
import { DEFAULT_LOCALE } from '../i18n/client';

interface VideoListParams {
  tabType: 'movie' | 'tv' | 'anime' | 'short-drama';
  filters?: {
    type?: string;
    region?: string;
    year?: string;
  };
  initialData?: {
    videos: VideoItem[];
    hasMore: boolean;
    total: number;
    isLoading: boolean;
    isError: boolean;
    error?: string;
  };
  locale?: string;
}

// APIè¯·æ±‚å‚æ•°ç±»å‹
interface ApiParams {
  artType: string;
  pageIndex: number;
  pageSize: number;
  contentType?: string;
  area?: string;
  releaseYear?: string;
  videoType?: string;
  reelsContentType?: string;
  release_year?: string;
  [key: string]: any; // å…è®¸æ·»åŠ å…¶ä»–å‚æ•°
}

// APIå“åº”ç±»å‹
interface ApiResponse {
  list: Array<{
    id: number;
    name: string;
    area?: string;
    artType?: string;
    author?: string;
    authorAvatar?: string;
    authorName?: string;
    commentCount?: number;
    contentType?: string;
    coverHSrc?: string;
    coverHSrcLocal?: string;
    coverSrc?: string;
    coverSrcLocal?: string;
    createdAt?: string;
    deletedAt?: string;
    director?: string;
    duration?: string;
    introduction?: string;
    isFollowerId?: string;
    isLikedId?: string;
    lastWatchSet?: string;
    lastWatchTime?: string;
    level?: string;
    likeCount?: number;
    nameEn?: string;
    playable?: string;
    reelsContentType?: string;
    releaseYear?: string;
    score?: string;
    setsCount?: string;
    shareCount?: number;
    starring?: string;
    summary?: string;
    updatedAt?: string;
    url?: string;
    urlLocal?: string;
    vid?: string;
    videoType?: string;
    [key: string]: any;
  }>;
  pageIndex: number;
  pageSize: number;
  platformInfo?: {
    showAds: string;
  };
  total: number;
}

// å°†tabTypeæ˜ å°„åˆ°APIæ‰€éœ€çš„artType
const tabTypeToArtType = {
  'movie': '0', // ç”µå½±
  'tv': '1', // ç”µè§†å‰§
  'anime': '2', // åŠ¨æ¼«
  'short-drama': '8' // çŸ­å‰§
};

/**
 * å¤„ç†è§†é¢‘åˆ—è¡¨æ•°æ®è·å–å’Œç­›é€‰çš„é’©å­å‡½æ•°
 */
export const useVideoList = ({ tabType, filters = {}, initialData, locale = DEFAULT_LOCALE }: VideoListParams) => {
  // ç”¨äºå­˜å‚¨æœåŠ¡ç«¯è·å–çš„åˆå§‹æ•°æ®
  const [serverData, setServerData] = useState<{
    videos: VideoItem[];
    hasMore: boolean;
    total: number;
  } | null>(initialData ? {
    videos: initialData.videos,
    hasMore: initialData.hasMore,
    total: initialData.total
  } : null);
  
  // ç”¨äºå¼ºåˆ¶è§¦å‘ç»„ä»¶é‡æ–°æ¸²æŸ“
  const [forceUpdate, setForceUpdate] = useState(0);
  
  // è·Ÿè¸ªå½“å‰æ ‡ç­¾å’Œç­›é€‰æ¡ä»¶
  const [currentTabType, setCurrentTabType] = useState(tabType);
  const [currentFilters, setCurrentFilters] = useState(JSON.stringify(filters));
  
  // çŠ¶æ€æ ‡è®°
  const isLoadingRef = useRef(false);
  const isInitialLoadRef = useRef(true);
  const manualTriggerRef = useRef(false);
  
  // ç¼“å­˜ç­›é€‰æ¡ä»¶
  const filtersRef = useRef(filters);
  useEffect(() => {
    filtersRef.current = filters;
  }, [filters]);
  
  // å½“å‰é¡µæ•°
  const pageRef = useRef(0);
  
  // æ„å»ºç¼“å­˜é”®
  const swrKey = useMemo(() => 
    `videos-${tabType}-${JSON.stringify(filters)}-${locale}`, 
    [tabType, filters, locale]
  );
  
  // æ£€æµ‹æ ‡ç­¾ç±»å‹æˆ–ç­›é€‰æ¡ä»¶å˜åŒ–
  useEffect(() => {
    const newTabType = tabType;
    const newFilters = JSON.stringify(filters);
    
    if (currentTabType !== newTabType || currentFilters !== newFilters) {
      // console.log(`æ£€æµ‹åˆ°å˜åŒ–: æ ‡ç­¾ä» ${currentTabType} å˜ä¸º ${newTabType}, æˆ–ç­›é€‰æ¡ä»¶å˜æ›´`);
      
      // æ›´æ–°å½“å‰çŠ¶æ€
      setCurrentTabType(newTabType);
      setCurrentFilters(newFilters);
      
      // é‡ç½®çŠ¶æ€
      pageRef.current = 0;
      isLoadingRef.current = false;
      isInitialLoadRef.current = true;
      
      // å¦‚æœæ˜¯æ ‡ç­¾å˜æ›´ï¼Œä¿ç•™åˆå§‹æ•°æ®
      if (initialData && currentTabType !== newTabType) {
        setServerData({
          videos: initialData.videos,
          hasMore: initialData.hasMore,
          total: initialData.total
        });
      } else {
        // å¦‚æœæ˜¯ç­›é€‰æ¡ä»¶å˜æ›´ï¼Œæ¸…ç©ºæœåŠ¡ç«¯æ•°æ®
        setServerData(null);
      }
      
      // è§¦å‘æ•°æ®é‡æ–°åŠ è½½
      manualTriggerRef.current = true;
      // å¼ºåˆ¶é‡æ–°æ¸²æŸ“ä»¥ç¡®ä¿çŠ¶æ€æ›´æ–°
      setForceUpdate(prev => prev + 1);
    }
  }, [tabType, filters, initialData, currentTabType, currentFilters]);
  
  // é¡µé¢åŠ è½½æˆ–å‚æ•°ä¿®æ”¹æ—¶åˆå§‹åŒ–æ•°æ®è·å–å‡½æ•°
  const fetchVideoList = useCallback(async (page: number) => {
    if (isLoadingRef.current && page > 1) return null;
    
    // console.log(`è¯·æ±‚è§†é¢‘åˆ—è¡¨: é¡µç =${page}, æ ‡ç­¾=${tabType}`);
    isLoadingRef.current = true;
    
    try {
      const params: ApiParams = {
        videoType: tabTypeToArtType[tabType] === '8' ? '3' : '0',
        artType: tabTypeToArtType[tabType],
        pageIndex: page,
        pageSize: 18
      };
      
      // æ·»åŠ ç­›é€‰æ¡ä»¶
      if (filtersRef.current.region && filtersRef.current.region !== 'all') {
        params.area = filtersRef.current.region;
      }
      
      if (filtersRef.current.year && filtersRef.current.year !== 'all') {
        params.releaseYear = filtersRef.current.year;
      }
      
      // æ ¹æ®æ ‡ç­¾ç±»å‹å¤„ç†å†…å®¹ç±»å‹ç­›é€‰
      if (tabType === 'short-drama') {
        // çŸ­å‰§ä½¿ç”¨reels_content_typeå‚æ•°
        if (filtersRef.current.type && filtersRef.current.type !== 'all') {
          params.reelsContentType = filtersRef.current.type;
        }
      } else {
        // å…¶ä»–æ ‡ç­¾ä½¿ç”¨content_typeå‚æ•°
        if (filtersRef.current.type && filtersRef.current.type !== 'all') {
          params.contentType = filtersRef.current.type;
        }
      }
      
      // console.log(`è¯·æ±‚å‚æ•°:`, params);
      const response = await getVideoList(params) as ApiResponse;
      // console.log(`è¯·æ±‚æˆåŠŸï¼Œè·å–åˆ° ${response.list.length} æ¡æ•°æ®`);
      
      // åˆ¤æ–­æ˜¯å¦æœ‰æ›´å¤šæ•°æ®
      const currentItemsCount = response.list.length;
      const totalReceivedItems = (page - 1) * params.pageSize + currentItemsCount;
      const hasMore = totalReceivedItems < response.total && currentItemsCount === params.pageSize;
      
      // æ ¹æ®localeå†³å®šä½¿ç”¨å“ªä¸ªæ ‡é¢˜ä½œä¸ºä¸»æ ‡é¢˜
      const formattedVideos = response.list.map((item, index) => {
        // å½“localeä¸æ˜¯cnæ—¶ï¼Œä¼˜å…ˆä½¿ç”¨è‹±æ–‡æ ‡é¢˜
        const primaryTitle = (locale === 'cn' || locale === 'tw') ? item.name : item.nameEn??item.name;
        
          
          
        return {
          id: item.ID || String(item.ID) || `item-${page}-${index}`,
          title: primaryTitle,
          cover: item.coverSrc || item.coverHSrc || '',
          description: item.introduction || item.summary || '',
          score: item.score ? parseFloat(item.score) : 0,
          year: item.releaseYear || item.release_year || '',
          category: item.contentType || item.content_type || item.reelsContentType || item.reels_content_type || '',
          region: item.area || '',
          updateInfo: item.lastWatchSet || item.last_watch_set || '',
          tags: []
        };
      });
      
      // é¦–æ¬¡åŠ è½½å®Œæˆ
      isInitialLoadRef.current = false;
      
      // è¿”å›å¤„ç†åçš„ç»“æœ
      return {
        videos: formattedVideos,
        hasMore,
        total: response.total || 0,
        page
      };
    } catch (error) {
      console.error('è·å–è§†é¢‘åˆ—è¡¨å¤±è´¥:', error);
      return {
        videos: [],
        hasMore: false,
        total: 0,
        page,
        error: error instanceof Error ? error.message : 'æœªçŸ¥é”™è¯¯'
      };
    } finally {
      isLoadingRef.current = false;
    }
  }, [tabType, locale]);
  
  // å­˜å‚¨æ‰€æœ‰å·²åŠ è½½çš„è§†é¢‘
  const [videoState, setVideoState] = useState<{
    videos: VideoItem[];
    hasMore: boolean;
    isLoading: boolean;
    error: string | null;
    total: number;
  }>({
    videos: initialData?.videos || [],
    hasMore: initialData?.hasMore || false,
    isLoading: false,
    error: null,
    total: initialData?.total || 0
  });
  
  // åŠ è½½æ›´å¤šè§†é¢‘
  const loadMore = useCallback(async () => {
    // é¿å…é‡å¤è¯·æ±‚
    if (isLoadingRef.current || !videoState.hasMore) {
      // console.log('è·³è¿‡åŠ è½½: æ­£åœ¨åŠ è½½ä¸­æˆ–æ²¡æœ‰æ›´å¤šæ•°æ®');
      return;
    }
    
    // console.log(`å¼€å§‹åŠ è½½æ›´å¤šï¼Œå½“å‰é¡µç : ${pageRef.current}`);
    
    // è®¾ç½®åŠ è½½çŠ¶æ€
    setVideoState(prev => ({
      ...prev,
      isLoading: true
    }));
    
    try {
      // è·å–ä¸‹ä¸€é¡µæ•°æ®
      const nextPage = pageRef.current + 1;
      const result = await fetchVideoList(nextPage);
      
      // å¦‚æœè·å–å¤±è´¥æˆ–æ²¡æœ‰æ•°æ®ï¼Œåœæ­¢å¤„ç†
      if (!result || result.error) {
        throw new Error(result?.error || 'åŠ è½½å¤±è´¥');
      }
      
      // æ›´æ–°é¡µç 
      pageRef.current = nextPage;
      
      // æ›´æ–°è§†é¢‘çŠ¶æ€ï¼Œåˆå¹¶æ–°æ—§æ•°æ®
      setVideoState(prev => {
        // åˆ›å»ºä¸´æ—¶æ˜ å°„å»é‡
        const videoMap = new Map<string, VideoItem>();
        
        // æ·»åŠ å·²æœ‰è§†é¢‘
        prev.videos.forEach(video => {
          videoMap.set(video.id, video);
        });
        
        // æ·»åŠ æ–°è§†é¢‘
        result.videos.forEach(video => {
          videoMap.set(video.id, video);
        });
        
        // å°†æ˜ å°„è½¬æ¢å›æ•°ç»„
        const newVideos = Array.from(videoMap.values());
        
        return {
          videos: newVideos,
          hasMore: result.hasMore,
          isLoading: false,
          error: null,
          total: result.total
        };
      });
      
      // console.log(`åŠ è½½æ›´å¤šå®Œæˆï¼Œå·²åŠ è½½ ${pageRef.current} é¡µ`);
    } catch (error) {
      console.error('åŠ è½½æ›´å¤šå¤±è´¥:', error);
      setVideoState(prev => ({
        ...prev,
        isLoading: false,
        error: error instanceof Error ? error.message : 'åŠ è½½å¤±è´¥'
      }));
    }
  }, [videoState.hasMore, fetchVideoList]);
  
  // é‡æ–°åŠ è½½å…¨éƒ¨æ•°æ®
  const reloadData = useCallback(async () => {
    // console.log('é‡æ–°åŠ è½½æ‰€æœ‰æ•°æ®');
    
    // é‡ç½®çŠ¶æ€
    pageRef.current = 0;
    isLoadingRef.current = true;
    
    // è®¾ç½®åŠ è½½çŠ¶æ€
    setVideoState(prev => ({
      ...prev,
      isLoading: true,
      error: null,
      videos: []
    }));
    
    try {
      // è·å–ç¬¬ä¸€é¡µæ•°æ®
      const result = await fetchVideoList(0);
      
      // å¦‚æœè·å–å¤±è´¥æˆ–æ²¡æœ‰æ•°æ®ï¼Œåœæ­¢å¤„ç†
      if (!result || result.error) {
        throw new Error(result?.error || 'åŠ è½½å¤±è´¥');
      }
      
      // æ›´æ–°è§†é¢‘çŠ¶æ€
      setVideoState({
        videos: result.videos,
        hasMore: result.hasMore,
        isLoading: false,
        error: null,
        total: result.total
      });
      
      // console.log(`é‡æ–°åŠ è½½å®Œæˆï¼Œè·å–åˆ° ${result.videos.length} æ¡æ•°æ®`);
    } catch (error) {
      console.error('é‡æ–°åŠ è½½å¤±è´¥:', error);
      setVideoState(prev => ({
        ...prev,
        isLoading: false,
        error: error instanceof Error ? error.message : 'åŠ è½½å¤±è´¥'
      }));
    }
  }, [fetchVideoList]);
  
  // é¦–æ¬¡åŠ è½½æˆ–å‚æ•°å˜åŒ–æ—¶è‡ªåŠ¨åŠ è½½
  useEffect(() => {
    // å¦‚æœæ˜¯æ‰‹åŠ¨è§¦å‘ï¼Œç›´æ¥åŠ è½½
    if (manualTriggerRef.current) {
      // console.log('å‚æ•°å˜åŒ–ï¼Œæ‰‹åŠ¨è§¦å‘æ•°æ®åŠ è½½');
      manualTriggerRef.current = false;
      reloadData();
      return;
    }
    
    // å¦‚æœæœ‰åˆå§‹æ•°æ®ä¸”æ˜¯é¦–æ¬¡åŠ è½½ï¼Œä¸éœ€è¦è‡ªåŠ¨åŠ è½½
    if (initialData && isInitialLoadRef.current && serverData) {
      // console.log('ä½¿ç”¨åˆå§‹æ•°æ®ï¼Œä¸éœ€è¦è‡ªåŠ¨åŠ è½½');
      setVideoState({
        videos: serverData.videos,
        hasMore: serverData.hasMore,
        isLoading: false,
        error: null,
        total: serverData.total
      });
      isInitialLoadRef.current = false;
      return;
    }
    
    // è‡ªåŠ¨åŠ è½½ç¬¬ä¸€é¡µ
    if (isInitialLoadRef.current) {
      // console.log('é¦–æ¬¡åŠ è½½ï¼Œè‡ªåŠ¨è¯·æ±‚ç¬¬ä¸€é¡µæ•°æ®');
      reloadData();
    }
  }, [initialData, serverData, reloadData, forceUpdate]);
  
  // å¤„ç†ç­›é€‰å˜åŒ–
  const handleFilterChange = useCallback(() => {
    return reloadData();
  }, [reloadData]);
  
  // è®¡ç®—æœ€ç»ˆçŠ¶æ€
  const isEmpty = videoState.videos.length === 0 && !videoState.isLoading;
  
  return {
    videos: videoState.videos,
    hasMore: videoState.hasMore,
    total: videoState.total,
    isLoading: videoState.isLoading,
    isLoadingMore: videoState.isLoading && pageRef.current > 1,
    isEmpty,
    error: videoState.error,
    loadMore,
    handleFilterChange,
    currentPage: pageRef.current
  };
}; 