'use client';

import React, { useMemo, useCallback } from 'react';
import { MdOutlineSearch } from 'react-icons/md';
import { useRouter } from 'next/navigation';
import useAppStore from '@/app/store/useAppStore';
import { useVideoList } from '@/app/hooks/useVideoList';
import CategoryTabs from './CategoryTabs';
import VideoList from './VideoList';
import type { CategoryType } from '@/app/store/useAppStore';
import type { VideoItem } from '@/app/types/video';

interface ClientVideoListProps {
  tabType: 'movie' | 'tv' | 'anime' | 'short-drama';
  title: string;
  categories: {
    title: string;
    items: CategoryType[];
  }[];
  translations: Record<string, string>;
  initialData: {
    videos: VideoItem[];
    hasMore: boolean;
    total: number;
    isLoading: boolean;
    isError: boolean;
    error?: string;
  };
}

function ClientVideoList({ 
  tabType, 
  title, 
  categories, 
  translations, 
  initialData 
}: ClientVideoListProps) {
  const router = useRouter();
  const { setCurrentTab, filter, settings } = useAppStore();
  
  // ä½¿ç”¨ useMemo ç¼“å­˜ç­›é€‰æ¡ä»¶ï¼Œé¿å…ä¸å¿…è¦çš„çŠ¶æ€æ›´æ–°
  const filters = useMemo(() => ({
    type: filter.type || 'all',
    region: filter.region || 'all',
    year: filter.year || 'all'
  }), [filter.type, filter.region, filter.year]);
  
  // ä½¿ç”¨ useMemo ç¼“å­˜ hook å‚æ•°å¯¹è±¡ï¼Œé¿å…å¯¹è±¡å¼•ç”¨å˜åŒ–
  const videoListParams = useMemo(() => ({
    tabType, 
    filters,
    initialData,
    locale: settings.language
  }), [tabType, filters, initialData, settings.language]);
  
  // ä½¿ç”¨ç¼“å­˜çš„å‚æ•°è°ƒç”¨ hook
  const { 
    videos, 
    hasMore, 
    isLoading, 
    isLoadingMore,
    isEmpty,
    error, 
    loadMore,
    handleFilterChange,
    currentPage
  } = useVideoList(videoListParams);

  // console.log('useVideoList');
  
  // ç¼“å­˜æœç´¢ç‚¹å‡»å¤„ç†å‡½æ•°
  const handleSearchClick = useCallback(() => {
    router.push('/search');
  }, [router]);
  
  // ç¼“å­˜æœ¬åœ°åŒ–æ ‡é¢˜è·å–å‡½æ•°
  const getLocalizedTitle = useCallback(() => {
    switch (tabType) {
      case 'movie':
        return translations.artTypeFilm;
      case 'tv':
        return translations.artTypeTeleplay;
      case 'anime':
        return translations.artTypeEntertainment;
      case 'short-drama':
        return translations.artTypeVariety;
      default:
        return title;
    }
  }, [tabType, translations, title]);
  
  // ä½¿ç”¨ useMemo ç¼“å­˜ VideoList ç»„ä»¶çš„ props
  const videoListProps = useMemo(() => ({
    videos,
    loadMore,
    isLoading,
    isLoadingMore,
    isEmpty,
    hasError: !!error,
    hasMore,
    tabType,
    emptyMessage: translations.main_noData,
    translations,
    currentPage: currentPage || 0
  }), [videos, loadMore, isLoading, isLoadingMore, isEmpty, error, hasMore, tabType, translations, currentPage]);
  
  // ç¼“å­˜ CategoryTabs ç»„ä»¶çš„ props
  const categoryTabsProps = useMemo(() => ({
    categories,
    tabType,
    translations
  }), [categories, tabType, translations]);
  
  return (
    <div className="video-list-page">
      {/* é¡¶éƒ¨æœç´¢æ  */}
      <div className="sticky top-0 z-10 bg-white dark:bg-neutral-900 shadow-sm">
        <div className="flex items-center justify-between p-2">
          <h1 className="text-xl font-bold">{getLocalizedTitle()}</h1>
          <button 
            className="p-2 rounded-full bg-neutral-100 dark:bg-neutral-800"
            onClick={handleSearchClick}
          >
            <MdOutlineSearch size={20} />
          </button>
        </div>
        
        {/* åˆ†ç±»æ ‡ç­¾ */}
        <CategoryTabs {...categoryTabsProps} />
      </div>
      
      {/* è§†é¢‘åˆ—è¡¨ */}
      <VideoList {...videoListProps} />
    </div>
  );
}

// ä½¿ç”¨ React.memo åŒ…è£…ç»„ä»¶ï¼Œé¿å…ä¸å¿…è¦çš„é‡æ¸²æŸ“
export default React.memo(ClientVideoList); 