import React, { useEffect, useState } from 'react';
import { IoCloseCircleOutline } from 'react-icons/io5';
import { FaDownload, FaApple, FaAndroid } from 'react-icons/fa';

import { DOWNLOAD_LINK, ANDROID_DOWNLOAD_LINK, IOS_DOWNLOAD_LINK } from '@/appConfig';

interface DownloadAppModalProps {
  isOpen: boolean;
  onClose: () => void;
  translations: {
    downloadTitle?: string;
    downloadDesc?: string;
    downloadBtn?: string;
    cancelBtn?: string;
    download_app_title?: string;
    download_app_desc?: string;
    download_app_btn?: string;
    download_app_cancel?: string;
    download_app_short_title?: string;
    download_app_short_desc?: string;
    download_app_detail_title?: string;
    download_app_detail_desc?: string;
    general_watch_now?: string;
    general_hold?: string;
    [key: string]: string | undefined;
  };
  appLink?: string;
  videoTitle?: string;
  modalType?: 'default' | 'short' | 'detail';
}

// å¹³å°ç±»å‹
type Platform = 'ios' | 'android' | 'unknown';

export default function DownloadAppModal({
  isOpen,
  onClose,
  translations,
  appLink,
  videoTitle = '',
  modalType = 'default'
}: DownloadAppModalProps) {
  // å¹³å°çŠ¶æ€
  const [platform, setPlatform] = useState<Platform>('unknown');
  const [downloadUrl, setDownloadUrl] = useState(DOWNLOAD_LINK);

  // æ£€æµ‹ç”¨æˆ·å¹³å°
  useEffect(() => {
    const detectPlatform = (): Platform => {
      const userAgent = navigator.userAgent || navigator.vendor || (window as any).opera || '';
      
      // iOSè®¾å¤‡æ£€æµ‹
      if (/iPad|iPhone|iPod/.test(userAgent) && !(window as any).MSStream) {
        return 'ios';
      }
      
      // Androidè®¾å¤‡æ£€æµ‹
      if (/android/i.test(userAgent)) {
        return 'android';
      }
      
      return 'unknown';
    };
    
    const detectedPlatform = detectPlatform();
    setPlatform(detectedPlatform);
    
    // æ ¹æ®å¹³å°è®¾ç½®ä¸‹è½½é“¾æ¥
    if (detectedPlatform === 'ios') {
      setDownloadUrl(appLink || IOS_DOWNLOAD_LINK);
    } else if (detectedPlatform === 'android') {
      setDownloadUrl(appLink || ANDROID_DOWNLOAD_LINK);
    } else {
      setDownloadUrl(appLink || DOWNLOAD_LINK);
    }
  }, [appLink]);

  if (!isOpen) return null;

  // æ ¹æ®modalTypeé€‰æ‹©ç¿»è¯‘é”®
  let titleKey = 'download_app_title';
  let descKey = 'download_app_desc';
  
  if (modalType === 'short') {
    titleKey = 'download_app_short_title';
    descKey = 'download_app_short_desc';
  } else if (modalType === 'detail') {
    titleKey = 'download_app_detail_title';
    descKey = 'download_app_detail_desc';
  }

  // ä»i18nè·å–æ–‡æœ¬ï¼Œå¦‚æœæ²¡æœ‰åˆ™ä½¿ç”¨è‡ªå®šä¹‰æ–‡æœ¬
  const title = translations[titleKey] || translations.downloadTitle || 'ä¸‹è½½APPè§‚çœ‹å®Œæ•´å†…å®¹';
  let desc = translations[descKey] || translations.downloadDesc || 'å®‰è£…APPå³å¯è§‚çœ‹å…¨éƒ¨é«˜æ¸…è§†é¢‘ï¼Œæ— å¹¿å‘Šï¼Œæ”¯æŒç¦»çº¿ç¼“å­˜';
  
  // å¦‚æœæœ‰è§†é¢‘æ ‡é¢˜ï¼Œæ·»åŠ åˆ°æè¿°ä¸­
  if (videoTitle && modalType === 'detail') {
    desc = desc.replace('{title}', videoTitle);
  }
  
  const downloadBtn = translations.download_app_btn || translations.downloadBtn || 'ç«‹å³ä¸‹è½½';
  const cancelBtn = translations.download_app_cancel || translations.cancelBtn || 'å–æ¶ˆ';

  const handleDownload = () => {
    window.open(downloadUrl, '_blank');
  };

  // è·å–å¹³å°å›¾æ ‡
  const getPlatformIcon = () => {
    if (platform === 'ios') {
      return <FaApple className="text-lg mr-2" />;
    } else if (platform === 'android') {
      return <FaAndroid className="text-lg mr-2" />;
    } else {
      return <FaDownload className="text-lg mr-2" />;
    }
  };

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center">
      {/* èƒŒæ™¯é®ç½© */}
      <div 
        className="absolute inset-0 bg-black bg-opacity-70"
        onClick={onClose}
      ></div>
      
      {/* å¼¹æ¡†å†…å®¹ - ä¿®æ”¹å®½åº¦è®¾ç½®ï¼Œç¡®ä¿ä¸è¶…è¿‡ç§»åŠ¨å®¹å™¨ */}
      <div className="relative w-[90%] max-w-[300px] bg-white dark:bg-gray-800 rounded-lg shadow-xl overflow-hidden z-10">
        {/* å…³é—­æŒ‰é’® */}
        <button 
          onClick={onClose}
          className="absolute top-3 right-3 text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200"
        >
          <IoCloseCircleOutline className="text-2xl" />
        </button>
        
        {/* å†…å®¹åŒºåŸŸ */}
        <div className="p-6">
          <h3 className="text-lg font-bold text-center mb-4">{title}</h3>
          
          <div className="flex justify-center mb-4">
            <div className="w-16 h-16 rounded-full bg-blue-100 dark:bg-blue-900 flex items-center justify-center">
              {platform === 'ios' ? (
                <FaApple className="text-3xl text-blue-500" />
              ) : platform === 'android' ? (
                <FaAndroid className="text-3xl text-blue-500" />
              ) : (
                <FaDownload className="text-2xl text-blue-500" />
              )}
            </div>
          </div>
          
          <p className="text-sm text-gray-600 dark:text-gray-300 text-center mb-6">
            {desc}
          </p>
          
          <div className="flex flex-col gap-3">
            <button
              onClick={handleDownload}
              className="w-full py-2.5 bg-[rgb(9,161,228)] text-white rounded-full font-medium flex items-center justify-center"
            >
              {getPlatformIcon()}
              {downloadBtn}
            </button>
            
            <button
              onClick={onClose}
              className="w-full py-2.5 bg-gray-200 dark:bg-gray-700 text-gray-700 dark:text-gray-200 rounded-full font-medium"
            >
              {cancelBtn}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
} 