'use client';

import React, { useState, useCallback } from 'react';
import Image from 'next/image';
import { useRouter } from 'next/navigation';
import { MdPlayCircleOutline } from 'react-icons/md';
import useAppStore from '@/app/store/useAppStore';
import type { VideoItem } from '@/app/types/video';

interface VideoCardProps {
  video: VideoItem;
  tabType: 'movie' | 'tv' | 'anime' | 'short-drama';
  showDownloadTip?: () => void;
  translations: Record<string, string>;
  isFirstPage?: boolean;
}

function VideoCard({ video, tabType, showDownloadTip, translations, isFirstPage = false }: VideoCardProps) {
  const router = useRouter();
  const { addPlayHistory } = useAppStore();
  const [imageError, setImageError] = useState(false);
  
  // å¤„ç†è§†é¢‘ç‚¹å‡»äº‹ä»¶
  const handleClick = useCallback(() => {
    // å¦‚æœæ˜¯çŸ­å‰§ï¼Œæ˜¾ç¤ºä¸‹è½½æç¤º
    if (tabType === 'short-drama') {
      showDownloadTip?.();
      return;
    }
    
    // æ·»åŠ åˆ°æ’­æ”¾å†å²
    const historyItem = {
      id: video.id,
      title: video.title,
      poster: video.cover, // ä½¿ç”¨coverä½œä¸ºposter
      category: video.category,
      year: video.year,
      region: video.region
    };
    
    addPlayHistory(historyItem);
    
    // å¯¼èˆªåˆ°è¯¦æƒ…é¡µé¢
    router.push(`/detail/${video.id}`);
  }, [video, tabType, showDownloadTip, router, addPlayHistory]);
  
  // åŠ è½½å¤±è´¥æ—¶çš„å ä½å›¾
  const onImageError = useCallback(() => {
    setImageError(true);
  }, []);
  
  const noImageText = translations.general_no_image || 'æ— å›¾ç‰‡';
  
  return (
    <div 
      className="video-card relative w-full pb-2 cursor-pointer" 
      onClick={handleClick}
    >
      <div className="poster-wrapper relative aspect-[2/3] overflow-hidden rounded-lg mb-2 bg-gray-200 dark:bg-neutral-800">
        {!imageError ? (
          <Image
            src={video.cover} // ä½¿ç”¨coverè€Œä¸æ˜¯poster
            alt={video.title}
            fill
            sizes="(max-width: 768px) 33vw, 20vw"
            className="object-cover transition-transform duration-300 hover:scale-110"
            onError={onImageError}
            priority={isFirstPage}
            unoptimized={tabType === 'short-drama'}
          />
        ) : (
          <div className="w-full h-full flex items-center justify-center bg-gray-300 dark:bg-gray-700">
            <span className="text-gray-500 dark:text-gray-400 text-sm">{noImageText}</span>
          </div>
        )}
        
        {/* æ’­æ”¾æŒ‰é’®æ‚¬æµ®å±‚ */}
        <div className="play-overlay absolute inset-0 flex items-center justify-center opacity-0 bg-black bg-opacity-40 transition-opacity duration-300 hover:opacity-100">
          <MdPlayCircleOutline className="text-white text-5xl" />
        </div>
      </div>
      
      {/* è§†é¢‘æ ‡é¢˜ */}
      <h3 className="text-sm font-medium truncate text-center">
        {video.title}
      </h3>
      
    </div>
  );
}

// ä¼˜åŒ–ï¼šä½¿ç”¨ React.memo åŒ…è£…ç»„ä»¶
// æ·»åŠ è‡ªå®šä¹‰æ¯”è¾ƒå‡½æ•°ï¼Œåªæœ‰å½“ç›¸å…³å±æ€§å˜åŒ–æ—¶æ‰é‡æ–°æ¸²æŸ“
export default React.memo(VideoCard, (prevProps, nextProps) => {
  return (
    prevProps.video.id === nextProps.video.id &&
    prevProps.tabType === nextProps.tabType &&
    prevProps.isFirstPage === nextProps.isFirstPage &&
    prevProps.translations.general_no_image === nextProps.translations.general_no_image
  );
}); 