import CryptoES from 'crypto-es'
import {
  FETCH_TIME_OUT,
  appSecret,
  cryptMode,
} from '@/appConfig'

export const hasChinese = str => /[\u4e00-\u9fff]/.test(str)

export const sleep = delay => new Promise(resolve => setTimeout(resolve, delay))

export const encodeAes = str => {
  const key = CryptoES.enc.Utf8.parse(appSecret)

  const encryptedData = CryptoES.AES.encrypt(str, key, {
    iv: key,
    mode: CryptoES.mode.CBC,
    padding: CryptoES.pad.Pkcs7,
  })
  return encryptedData.ciphertext.toString()
}

export const decodeAes = str => {
  const key = CryptoES.enc.Utf8.parse(appSecret);
  const encryptedHexStr = CryptoES.enc.Hex.parse(str)
  const encryptedBase64Str = CryptoES.enc.Base64.stringify(encryptedHexStr)

  const decryptedData = CryptoES.AES.decrypt(encryptedBase64Str, key, {
    iv: key,
    mode: CryptoES.mode.CBC,
    padding: CryptoES.pad.Pkcs7,
  });

  return decryptedData.toString(CryptoES.enc.Utf8)

}

export const encodeRC4 = str => {
  const key = CryptoES.enc.Utf8.parse(appSecret)
  const encrypted = CryptoES.RC4.encrypt(str, key)
  return encrypted.ciphertext.toString()
}

export const decodeRC4 = str => {
  const key = CryptoES.enc.Utf8.parse(appSecret)
  const decrypted = CryptoES.RC4.decrypt(str, key)
  return decrypted.toString(CryptoES.enc.Utf8)
}

export const isObject = v => Object.prototype.toString.call(v) === '[object Object]'
export const isFormBody = v => v instanceof FormData

export const setStorage = async (key, value) => {
  try {
    if (!isObject(value)) {
      throw new Error('[setStorage]: value must be "object"')
    } else {
      await localStorage.setItem(key, JSON.stringify(value))
    }
  } catch (err) {
    console.error(`[setStorage]: ${err}`)
    await Promise.reject(new Error(`[setStorage]: ${err}`))
  }
}

export const getStorage = async (key) => {
  try {
    const res = await localStorage.getItem(key)
    if (res) {
      return JSON.parse(res)
    }
  } catch (err) {
    console.error(`[getStorage]: ${err}`)
    await Promise.reject(new Error(`[getStorage]: ${err}`))
  }
}

export const clearStorageItem = async key => {
  try {
    await localStorage.removeItem(key)
  } catch (err) {
    console.error(`[removeItem]: ${err}`)
    await Promise.reject(new Error(`[removeItem]: ${err}`))
  }
}

export const clearStorage = async () => {
  try {
    await localStorage.clear()
  } catch (err) {
    console.error(`[clearStorage]: ${err}`)
    await Promise.reject(new Error(`[clearStorage]: ${err}`))
  }
}


export const getRandomElements = (arr, count) => {
  if (!arr || !arr.length || !Array.isArray(arr)) return []
  const shuffled = arr?.slice(0)
  let i = arr.length
  const min = i - count
  let temp
  let index
  while (i -- > min) {
    index = Math.floor((i + 1) * Math.random())
    temp = shuffled[index]
    shuffled[index] = shuffled[i]
    shuffled[i] = temp
  }
  return shuffled?.slice(min)
}

export const randomNumber = (min, max) => Math.floor(Math.random() * (max - min + 1)) + min

export const shuffleArray = arr => {
  let curId = arr.length
  while (curId !== 0) {
    const randId = Math.floor(Math.random() * curId)
    curId -= 1
    const tmp = arr[curId]
    arr[curId] = arr[randId]
    arr[randId] = tmp;
  }
  return arr
}

export const allSettled = arr => {
  if (!Promise.allSettled) {
    return Promise.all(arr.map(p => Promise.resolve(p).then(res => res, error => error)))
  } else {
    return Promise.allSettled(arr)
  }
}

const buildUrl = (url, encrypt, secretKey) => {
  const queryParams = encrypt
    ? `c_m=${cryptMode}`
    : `dev=1&ak=${secretKey}&c_m=${cryptMode}`
  return `${url}${url.includes('?') ? '&' : '?'}${queryParams}`;
};

export const fetchHandle = ({
  url,
  method = 'GET',
  headers = {},
  body = {},
  token = '',
  timeout = FETCH_TIME_OUT,
  encrypt = 0,
  secretKey = '',
}, signal) => {
  if (!url) throw new Error('Missing params: url');
  const tokenStr = token ? `Bearer ${token}` : '';
  const headersOpt = {
    'Content-Type': 'application/json',
    ...headers,
    ...(tokenStr && { Authorization: tokenStr }),
  };

  const urlStr = buildUrl(url, encrypt, secretKey);

  const isValidBody = ['POST', 'PUT', 'PATCH'].includes(method.toUpperCase()) && body && Object.keys(body).length;
  
  let bodyData;
  if (isValidBody) {
    if (encrypt) {
      bodyData = encodeAes(JSON.stringify(body));
    } else {
      bodyData = JSON.stringify(body);
    }
  }

  const fetchOptions = {
    method,
    headers: headersOpt,
    body: isValidBody ? bodyData : undefined,
    signal,
  };

  const isServer = typeof window === 'undefined';

  let controller;
  let timeoutId;
  
  if (!isServer) {
    controller = new AbortController();
    const { signal: controllerSignal } = controller;

    if (signal) {
      signal.addEventListener('abort', () => controller.abort());
    }

    fetchOptions.signal = controllerSignal;
  }

  const request = fetch(urlStr, fetchOptions)
    .then(response => response.text())
    .then(text => text ? JSON.parse(text) : {})
    .finally(() => {
      if (!isServer && timeoutId) {
        clearTimeout(timeoutId);
      }
    });

  if (!isServer) {
    const timeoutPromise = new Promise((_, reject) => {
      timeoutId = setTimeout(() => {
        const e = 'Request timeout!';
        controller.abort(e);
        reject(new Error(e));
      }, timeout);
    });
    
    return Promise.race([request, timeoutPromise]);
  }
  
  return request;
};
