import { CategoryType } from '../store/useAppStore';
import { getCategoriesServerSide } from '../data/videos';
import { getConfig } from '@/appConfig';

// åˆ†ç±»æ•°æ®ç±»å‹
export interface CategoryGroup {
  title: string;
  items: CategoryType[];
}

// APIè¿”å›çš„åŸå§‹åˆ†ç±»æ•°æ®ç±»å‹
interface ApiCategory {
  name: string;
  type: string;
  data: string[];
}

/**
 * å¤„ç†APIè¿”å›çš„åŸå§‹åˆ†ç±»æ•°æ®
 * å°†æ•°å­—IDè½¬æ¢ä¸ºå®é™…æ ‡ç­¾æ–‡æœ¬
 * 
 * @param data APIè¿”å›çš„åŸå§‹åˆ†ç±»æ•°æ®
 * @param locale å½“å‰è¯­è¨€
 * @param tabType å½“å‰æ ‡ç­¾ç±»å‹
 * @returns å¤„ç†åçš„åˆ†ç±»æ•°æ®
 */
export const processCategories = (data: ApiCategory[], locale: string = 'cn', tabType: string = 'movie'): CategoryGroup[] => {
  if (!data || !Array.isArray(data)) {
    console.warn('æ— æ•ˆçš„åˆ†ç±»æ•°æ®æ ¼å¼');
    return [];
  }

  // ä½¿ç”¨appConfigä¸­å®šä¹‰çš„å¸¸é‡æ˜ å°„
  const { CATEGORY_TYPE, CONTENT_TYPE, REELS_CONTENT_TYPE, AREA, YEAR } = getConfig(locale);

  // å¤„ç†ç»“æœæ•°ç»„
  const result: CategoryGroup[] = [];

  // é¦–å…ˆæ ¹æ®tabTypeè¿‡æ»¤åˆé€‚çš„åˆ†ç±»æ•°æ®
  const filteredCategories = data.filter(category => {
    // ç¡®ä¿categoryå’Œnameå­˜åœ¨
    if (!category || !category.name) return false;
    
    // çŸ­å‰§ç‰¹æ®Šå¤„ç†
    if (tabType === 'short-drama') {
      return category.name === 'reels_content_type';
    } else {
      // éçŸ­å‰§æ’é™¤reels_content_type
      return category.name !== 'reels_content_type';
    }
  });

  // éå†è¿‡æ»¤åçš„åˆ†ç±»æ•°æ®
  filteredCategories.forEach(category => {
    // è·³è¿‡video_typeç±»å‹æˆ–æ— æ•ˆæ•°æ®
    if (!category || !category.name || category.name === 'video_type' || category.name === 'art_type' || !Array.isArray(category.data)) {
      return;
    }

    // æ ¹æ®åˆ†ç±»ç±»å‹ç¡®å®šæ ‡é¢˜å’Œå¤„ç†æ–¹å¼
    let title = '';
    let items: CategoryType[] = [];

    switch (category.name) {
      
      case 'content_type':
        title = CATEGORY_TYPE[0];
        items = category.data.map((id: string) => ({
          id,
          name: CONTENT_TYPE[id as keyof typeof CONTENT_TYPE] || id
        }));
        break;
      
      case 'reels_content_type':
        title = CATEGORY_TYPE[0];
        items = category.data.map((id: string) => ({
          id,
          name: REELS_CONTENT_TYPE[id as keyof typeof REELS_CONTENT_TYPE] || id
        }));
        break;
      
      case 'area':
        title = CATEGORY_TYPE[1];
        items = category.data.map((id: string) => ({
          id,
          name: AREA[id as keyof typeof AREA] || id
        }));
        break;
      
      case 'release_year':
        title = CATEGORY_TYPE[2];
        items = category.data.map((id: string) => ({
          id,
          name: id.length < 3 ? `${id}${YEAR}` : id
        }));
        // å¹´ä»½éœ€è¦ååºæ’åˆ—
        items.reverse();
        break;
      
      default:
        title = category.name;
        items = category.data.map((id: string) => ({
          id,
          name: String(id)
        }));
    }

    // æ·»åŠ "å…¨éƒ¨"é€‰é¡¹
    if (items.length > 0) {
      items.unshift({
        id: 'all',
        name: CATEGORY_TYPE[3]
      });
    }

    // æ·»åŠ åˆ°ç»“æœæ•°ç»„
    result.push({
      title,
      items
    });
  });

  // ç¡®ä¿æ¯ç§åˆ†ç±»ç±»å‹éƒ½æœ‰æ•°æ®
  if (tabType !== 'short-drama') {
    // æ£€æŸ¥å¹¶æ·»åŠ ç¼ºå¤±çš„åˆ†ç±»ç±»å‹
    const titles = result.map(group => group.title);
    
    // ç¡®ä¿ç±»å‹åˆ†ç±»å­˜åœ¨
    if (!titles.includes(CATEGORY_TYPE[0])) {
      result.push({
        title: CATEGORY_TYPE[0],
        items: [{
          id: 'all',
          name: CATEGORY_TYPE[3]
        }]
      });
    }
    
    // ç¡®ä¿åœ°åŒºåˆ†ç±»å­˜åœ¨
    if (!titles.includes(CATEGORY_TYPE[1])) {
      result.push({
        title: CATEGORY_TYPE[1],
        items: [{
          id: 'all',
          name: CATEGORY_TYPE[3]
        }]
      });
    }
    
    // ç¡®ä¿å¹´ä»½åˆ†ç±»å­˜åœ¨
    if (!titles.includes(CATEGORY_TYPE[2])) {
      result.push({
        title: CATEGORY_TYPE[2],
        items: [{
          id: 'all',
          name: CATEGORY_TYPE[3]
        }]
      });
    }
  }

  return result;
};

/**
 * é€šç”¨è·å–åˆ†ç±»æ•°æ®æ–¹æ³•
 * ä»APIè·å–åˆ†ç±»æ•°æ®å¹¶æ ¹æ®tabç±»å‹è¿›è¡Œç­›é€‰å¤„ç†
 * 
 * @param tab å½“å‰æ ‡ç­¾: 'movie' | 'tv' | 'anime' | 'short-drama'
 * @param locale å½“å‰è¯­è¨€
 * @returns å¤„ç†åçš„åˆ†ç±»æ•°æ®
 */
export const getCategories = async (
  tab: 'movie' | 'tv' | 'anime' | 'short-drama' = 'movie',
  locale: string = 'cn'
): Promise<CategoryGroup[]> => {
  try {
    // ä»APIè·å–æ‰€æœ‰åˆ†ç±»æ•°æ®ï¼Œä½¿ç”¨tabå‚æ•°
    const apiCategories = await getCategoriesServerSide(tab, locale);
    
    if (!apiCategories || apiCategories.length === 0) {
      console.warn('æœªèƒ½è·å–åˆ†ç±»æ•°æ®');
      return [];
    }
    
    return apiCategories;
  } catch (error) {
    console.error('è·å–åˆ†ç±»æ•°æ®å¤±è´¥', error);
    return [];
  }
};